.App {
    margin: 0 2.5%;
    display: flex; }

.todoList {
    width: 400px;
    min-height: 70vh;
    margin: 20% 5% 0 5%;
    border-radius: 7px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-color: white; }

.todoListHeader {
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: black;
    font-size: 25px;
    height: 8vh;
    border-bottom: 1px solid #cecfdd;
    background-color: #fbfbff;
    padding: 5%;
    border-radius: 7px 7px 0 0; }

.todoListDeleteButton {
    font-weight: 700;
    font-size: 1.5vw;
    background-color: transparent;
    border: none;
    color: black;
    outline: none;
    cursor: pointer;
    width: 10%;
    img {
        width: 100%; } }

.todoListContent {
    background-color: white;
    max-width: 100%;
    min-height: 45vh; }

@media screen and (max-width: 600px) {
    .todoList {
        min-height: 50vh; }
    .todoListHeader {
        font-size: 23px; }
    .todoListContent {
        min-height: 35vh; } }

@media screen and (max-width: 400px) {
    .App {
        margin: 0; }
    .todoList {
        width: 280px; }
    .todoListHeader {
        font-size: 20px;
        text-align: center; }
    .todoListDeleteButton {
        width: 15%; } }
