.App {
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
    max-width: 100%; }

@media screen and (max-width: 700px) {
    .App {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-bottom: 5%; } }

.error {
    border-color: red; }


.itemHeader {
    display: flex;
    justify-content: space-between; }

.logout {
    text-decoration: underline;
    cursor: pointer;
    &:hover {
        color: #008fe1; } }
